header,
main,
footer {
    display: flex;
    flex: 1;
    vertical-align: middle;
    padding: 10px;
    box-sizing: border-box;
}
header,
footer {
    background-color: #000;
    color: #fff;
}
header p,
footer p {
    margin: auto;
    line-height: 50px;
    display: table-cell;
}
header {
    margin-bottom: 20px;
}
footer {
    margin-top: 20px;
}